.header__menu {
  /* display: grid;
  grid-auto-flow: column;
  align-items: center; */
  display: flex;
  text-align: center;
  margin: 0px 20px 0px 0px;
}

.header__menu a{
  margin: 0px 0px 0px 20px;
}

.header__items {
  justify-content: end;
}

.header__items_photoRadius img {
  border-radius: 30px;
}

a.header__items_activeLink {
  color: rgb(138, 89, 45);
}

.header__login {
  margin-right: 15px;
}

.header__items:hover {
  color: rgb(145, 90, 8);
}

.header__items_color:hover {
  transform: scale(1.2);
  color: rgb(145, 90, 8);
}

.header__button_color {
  color: rgb(117, 113, 113);
  border: none;
  font-size: 16px;
  text-decoration: underline;
}

.header__button_hover:hover {
  color: rgb(138, 89, 45);
}

.header__profile {
  width: 30px;
  height: 30px;
}

.header__message {
  height: 30px;
  width: 30px;
}

.header__friends {
  height: 30px;
  width: 30px;
}

@media (max-width: 1150px) {
  .header__menu {
    font-size: 14px;
  }

  .header__button_color {
    font-size: 14px;
  }
}

@media (max-width: 1050px) {
  .header__menu {
    font-size: 12px;
  }

  .header__button_color {
    font-size: 12px;
  }
}

@media (max-width: 768px) {
  .header__button_color {
    font-size: 10px;
  }
  .header__menu{
    display: block;
    margin: 80px 0px 0px 0px;
  }

  .header__menu a{
    display: block;
    margin: 0px 0px 20px 0px;
    font-size: 16px;
  }

  .header__button_color {
    font-size: 16px;
    margin-right:15px;
  }
  
}



