/* .header {
  display: grid;
  grid-template: 1fr / 1fr minmax(auto, 120px) minmax(auto, 1280px) 1fr;
} */

.header:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(228, 228, 228, 0.9);
  z-index: 2;
}

.header__body {
  position: relative;
  display: flex;
  justify-content: space-between;
  height: 50px;
  align-items: center;
}

.header__logo {
  /* grid-column: 2/3; */
  text-align: center;
  vertical-align: middle;
  justify-content: center;
  z-index: 3;
}

.header__logo {
  flex: 0 0 100px;
  overflow: hidden;
}

.header__logo img {
  max-width: 100%;
  display: block;
}

.header__burger {
  margin-top: 3px;
  /* grid-column: 3/4; */
  display: none;
}

/* .header__menu {
  grid-column: 3/4;
  display: block;
} */

.header__menu {
  display: flex;
  z-index: 3;
}
@media (max-width: 1150px) {
  .header__menu {
    font-size: 10px;
  }
}

@media (max-width: 768px) {
  .header__menu {
    /* display: none; */
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(228, 228, 228);
    z-index: 1;
    font-size: 18px;
  }

  .header__menu_active {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    font-size: 18px;
  }

  .header__menu{
    display: block;
  }

  .header__burger {
    display: block;
    position: relative;
    width: 30px;
    height: 30px;
    right: 0;
    z-index: 3;
  }

  .header__burger_active {
    display: block;
    position: relative;
    width: 30px;
    height: 30px;
    right: 0;
    z-index: 3;
  }

  .header__burger_active span {
    position: absolute;
    background-color: rgb(117, 113, 113);
    left: 0;
    width: 100%;
    height: 1.5px;
    top: 14px;
    background-color: tomato;
  }

  .header__burger span {
    position: absolute;
    background-color: rgb(117, 113, 113);
    left: 0;
    width: 100%;
    height: 1.5px;
    top: 14px;
  }

  .header__burger:before,
  .header__burger:after {
    content: "";
    background-color: rgb(117, 113, 113);
    position: absolute;
    width: 30px;
    height: 2px;
    left: 0;
  }
  .header__burger:before {
    top: 5px;
  }
  .header__burger::after {
    bottom: 6px;
  }
}

